import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';

import Map from '../../components/map';
import ImageSlider from '../../components/imageSlider';
import LineGraph from '../../components/lineGraph';

const MONTHS = ['jan', 'feb', 'mar', 'apr', 'mai', 'jun', 'jul', 'aug', 'sep', 'okt', 'nov', 'des'];

const initialState = {
    newTrack: {},
};

@observer
class TracksDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTrack(props = this.props) {
		const { trackStore, userStore } = this.props.stores;
        await trackStore.load(props.track);
        const { track } = trackStore;
        await userStore.getWorkoutSummary(null, null, track.id);
    }

    back = () => {
        route(`/tracks/`, true);
    }

    componentWillMount() {
        this.loadTrack();
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.track !== this.props.track) {
            this.loadTrack(nextProps);
        }
    }

	render() {
		const { trackStore, userStore } = this.props.stores;
		const { track } = trackStore;
        const { gpx } = track;
        const {
            graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev,
			graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev,
        } = userStore;

		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <div class='d-flex justify-content-between'>
                    <div>
                        <button class='btn btn-link pl-0 fixed-top'
                            style='top: 50px; left: 5px; font-size: 25px; width: 40px;'
                            onClick={this.back}>
                            <i class='fas fa-arrow-circle-left' />
                        </button>
                    </div>
                    <div>
                        <h5 class='text-muted'>&nbsp;</h5>
                    </div>
                    <div>
                        <a class='btn btn-sm btn-primary float-right'
                            href={`/tracks/edit/${track.id}`}
                        >
                            <i class='fas fa-edit' /> Endre
                        </a>
                    </div>
                </div>

                <div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <div class='d-flex mb-2'>
                            {/* {JSON.stringify(dog, null, 4)} */}
                            <div class={`text-center border rounded-circle imageRounded`} style={track.image ? `background-image: url("${track.image.s3SmallLink}");` : ''}>
                                {!track.image && <i class='fas fa-map-marked text-muted mt-3' style='font-size: 40px;' />}
                            </div>
                            <div class='flex-grow-1 pl-3'>
                                <h5>{track.name}</h5>
                                {track.created}
                            </div>
                        </div>

                        <div class='w-100 mt-3 mb-4'>
                            <small class='mb-4'><small>
                                <table class='table table-sm font-weigth-lighter border-bottom'>
                                    <thead>
                                        <tr>
                                            <th style='padding: .2rem;'>Mnd</th>
                                            {util.range(1, 12).map(w => (
                                                <th class='text-right' style='padding: .2rem;'>{MONTHS[w - 1]}</th>
                                            ))}
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr>
                                            <th scope='row' style='padding: .2rem;'>2019</th>
                                            {util.range(1, 12).map(m => (
                                                <>
                                                    <td class='text-right' style='padding: .2rem;'>
                                                        {userStore.findWorkoutSummaryMonth(2019, m)}<br />
                                                        {userStore.findWorkoutSummaryMonth(2019, m, 'elevation')}<br />
                                                    </td>
                                                </>
                                            ))}
                                            <td class='text-muted' style='padding: .2rem;'>
                                                <nobr><i class='fas fa-road' /> km</nobr><br />
                                                <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                            </td>
                                        </tr>
                                        <tr>
                                            <th scope='row' style='padding: .2rem;'>2020</th>
                                            {util.range(1, 12).map(m => (
                                                <>
                                                    <td class='text-right' style='padding: .2rem;'>
                                                        {userStore.findWorkoutSummaryMonth(2020, m)}<br />
                                                        {userStore.findWorkoutSummaryMonth(2020, m, 'elevation')}<br />
                                                    </td>
                                                </>
                                            ))}
                                            <td class='text-muted' style='padding: .2rem;'>
                                                <nobr><i class='fas fa-road' /> km</nobr><br />
                                                <nobr><i class='fas fa-mountain' /> m</nobr><br />
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </small></small>
                                        
                            <LineGraph
                                stores={this.props.stores}
								width={500}
								height={150}
								showXTicks={1}
								xTicksVal={val => `uke ${val}`}
								paddingTop={30}
								lineFromXzero={true}
								lineFromXmax={true}

								dataLeft={[graphWorkoutSummaryWeekDistanceCurrent, graphWorkoutSummaryWeekDistancePrev]}
								legendLeft={['2020 Distanse', '2019 Distanse']}
								// yMaxLeft={30}
								// yMinLeft={0}
								showYTicksLeft={1}
								yTicksLeftPostfix={'km'}
								unitLeft={['km', 'km']}
								avgWindowLeft={[0, 0]}
								smoothLeft={[1, 1]}

								dataRight={[graphWorkoutSummaryWeekElevationCurrent, graphWorkoutSummaryWeekElevationPrev]}
								legendRight={['2020 Høydemeter', '2019 Høydemeter']}
								showYTicksRight={1}
								yTicksRightPostfix={'m'}
								unitRight={['m', 'm']}
								avgWindowRight={[0, 0]}
								smoothRight={[1, 1]}
                            />
                        </div>


                        {track.gpx && <div class='mb-2' style='height: 500px;'>
                            <Map stores={this.props.stores} mapId={track.id} geojson={track.gpx.geoJSON} gpxInfo={track.gpx.gpxInfo} />
                        </div>}


                        <div class='container-fluid mb-2'>
	                        <div class='row mt-2'>
	                            <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-road' /> Distance</small>
                                    <h3>
                                        {track.distanceKm} km
                                    </h3>
                                </div>
                                <div class='col text-center'>
                                    <small class='text-muted'><i class='fas fa-mountain' /> Elevation</small>
                                    <h3>
                                        {track.elevation} m
                                    </h3>
                                </div>
                            </div>
                        </div>

                        <div class='container-fluid mb-2 px-0'>
                            <div class='row'>
                                <div class='col-12'>
                                    <div class='d-flex mx-0'>
                                        {track.images && <ImageSlider images={track.images} />}
                                    </div>    
                                </div>
                            </div>
						</div>

						<div class='d-flex'>
							<a class='btn btn-primary mt-2 float-right'
								href={`/tracks/edit/${track.id}`}
							>
								<i class='fas fa-edit' /> Endre {track.name}
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default TracksDetail;
